<template>
	<view class="page">
		<!-- #ifdef MP-WEIXIN  -->
		<view v-if="showVideo" :style="liveplayerStyle" style="position: fixed; z-index: 1024">
			<view v-if="videoType=='rtmp'">
				<live-player style="width:100vw; height: 225px;" id="livePlayer" :src="playUrl" :orientation="direction" mode="live"
				 muted="false" object-fit='contain' autoplay @statechange="statechange" @error="error" />
				<cover-view style="width:100vw;display:flex;flex-direction:row;justify-content:space-between;position:absolute;right:0%;top:195px">
					<cover-view style="width: 25px;height:25px;margin-left:10px;" @tap="tapPlay">
						<cover-image style="width: 25px;height:25px" :src='isPlay ? pauseIcon : playIcon' />
					</cover-view>
				</cover-view>
			</view>
			<video v-if="videoType=='m3u8' || videoType=='httpflv'" :src="playUrl" controls="true" show-progress="false"
			 autoplay style="width:100vw">
			</video>
		</view>
		<!--  #endif -->
		<!-- #ifdef H5  -->
		<view v-if="showVideo" ref="video" class="video-js" style="position: fixed;width:100vw;height:225px"></view>
		<!--  #endif -->
		<!-- #ifdef APP-PLUS -->
		<view v-if="showVideo" class="video" style="position:fixed;width:100vw;height:225px">
			<video :src="playUrl" autoplay="true" controls="true" style="width:100%;height:100%;position:absolute;top:50%;left: 50%;transform: translate(-50%,-50%);">
			</video>
		</view>
		<!--  #endif -->
		<scroll-view :style="style" scroll-y="true">
			<view v-for="(item,index) in settingList" :key="index">
				<view class="bg-white radius-xl margin-top margin-bottom">
					<view class="cu-bar bg-white">
						<view class="action">
							<text>{{item.name}}</text>
						</view>
						<view class="action">
							<text class="text-df text-gray">{{i18n.deviceController.port}}:{{item.port}}</text>
						</view>
					</view>
					<view class="flex align-center justify-between  padding-bottom">
						<view class="margin-left margin-right-xl cuIcon-edit text-grey" style="font-size: 36upx;" v-if="item.settingType !='6'" @tap="openSetPortModal(item)"></view>
						<view class="flex-sub margin-right">
							<!-- 自定义 -->
							<view v-if="item.settingType=='0'">
								<form @submit="changeParam" :data-setting="item.setting" :data-port="item.port" :data-childPort="item.childPort"
								 data-change="custom">
									<view class="flex align-center justify-end ">
										<input :disabled="item.disabled" class="margin-right-sm ztnCustom-custominput flex-sub " style="border: 1upx solid rgba(135, 153, 163, 0.5)"
										 type='text' name="custom" :value="item.value"></input>
										<button :disabled="item.disabled" class="cu-btn block bg-grey" :style="{backgroundColor:}" style="padding-left: 14px;padding-right: 14px;"
										 hover-class="btn-hover-class" size='mini' form-type="submit">{{i18n.confirm}}</button>
									</view>
								</form>
							</view>
							<!-- 固定参数 -->
							<view v-else-if="item.settingType=='1'">
								<view id="fixedParam" class='flex align-center justify-end flex-wrap'>
									<view v-for="(arrItem,index) in item.paramList" :key="index" style="margin-left:20upx;margin-bottom:10upx;">
										<button :disabled="item.disabled" class="custom-button" size='mini' :style="(item.value== arrItem.value ? 'background:#8799a3;color:#fff;' : 'color:#8799a3')"
										 style="border: 1upx solid rgba(135, 153, 163, 0.5)" @tap="changeParam" :data-setting="item.setting"
										 :data-port="item.port" :data-childPort="item.childPort" :data-value="arrItem.value" data-change="fixed">{{arrItem.name}}</button>
									</view>
								</view>
							</view>
							<!-- 开关量 -->
							<view v-else-if="item.settingType=='2'">
								<view class="flex justify-end">
									<switch :disabled="item.disabled" class="grey sm" :checked="item.value" @change="changeParam"
									 :data-setting="item.setting" :data-port="item.port" :data-childPort="item.childPort" data-change='switch'></switch>
								</view>
							</view>
							<!-- 定时设置 -->
							<view v-else-if="item.settingType=='3'">
								<view class='flex justify-end'>
									<picker :disabled="item.disabled" class="flex-sub" mode="time" :value="item.value" @change="changeParam"
									 :data-setting="item.setting" :data-port="item.port" :data-childPort="item.childPort" data-change='timeSet'>
										<view class="ztnCustom-custominput " style="border: 1upx solid rgba(135, 153, 163, 0.5)">{{item.value}}</view>
									</picker>
								</view>
							</view>
							<!-- 时间段设置 -->
							<view v-else-if="item.settingType=='4'">
								<view class='flex justify-end'>
									<picker :disabled="item.disabled" class="flex-sub" mode="multiSelector" @change="changeParam" :data-setting="item.setting"
									 :data-port="item.port" :data-childPort="item.childPort" data-change='periodTime' :value="item.multiIndex"
									 :range="multiArray">
										<view class="ztnCustom-custominput" style="border: 1upx solid rgba(135, 153, 163, 0.5)">{{item.value}}</view>
									</picker>
								</view>
							</view>
							<!-- 无参数命令 -->
							<view v-else-if="item.settingType=='5'">
								<view class='flex  justify-end'>
									<button :disabled="item.disabled" class="cu-btn block bg-grey" style="padding-left: 14px;padding-right: 14px"
									 @tap="changeParam" :data-setting="item.setting" :data-port="item.port" :data-childPort="item.childPort"
									 data-change='nop'>{{i18n.confirm}}</button>
								</view>
							</view>
							<!-- 拖动条 -->
							<view v-else-if="item.settingType=='8'">
								<view class='flex justify-end '>
									<slider :disabled="item.disabled" class="flex-sub" style="margin-right:0upx;margin-left:0upx" show-value max="100"
									 min="0" step="5" :value="item.value" activeColor="#8799a3" block-size="20" @change="changeParam"
									 :data-setting="item.setting" :data-port="item.port" :data-childPort="item.childPort" data-change="slider"></slider>
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</scroll-view>
		<view v-if="!showVideo && cameraList.length != 0 && isLogin" style="height:100upx"></view>
		<view v-if="!showVideo && cameraList.length != 0 && isLogin" @tap="openCameraListModal" class="cu-avatar round lg bg-blue light"
		 style="width: 80upx;height: 80upx;font-size: 1.5em;position: fixed !important;z-index: 1024;right:40upx;bottom:20upx;">
			<text class="cuIcon-camera"></text>
		</view>

		<view class="cu-modal drawer-modal justify-start" :class="[showCameraListModal?'show':'']" @tap="hideCameraListModal">
			<view class="cu-dialog basis-lg" :style="cameraListModalStyle">
				<view class="cu-list menu text-left">
					<view class="cu-item arrow" v-for="(item,index) in cameraList" :key="index" @tap="tapCamera(item)">
						<view class="content">
							<view>
								<view>{{item.name}}</view> #{{item.deviceId}}
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>

		<view class="cu-modal bottom-modal" :class="[showErrorModal?'show':'']">
			<view class="cu-dialog">
				<view class="cu-bar bg-white">
					<view class="action text-red" @tap="hideErrorModal">{{i18n.confirm}}</view>
				</view>
				<view class="padding-xl">
					{{deviceError}}
				</view>
			</view>
		</view>

		<view v-if="setPortItem != null && setPortItem.settingType !='6' " style="position:fixed;width:100vw;height:100vh;bottom:0;z-index: 1024;background: rgba(0, 0, 0, 0.2)">
			<view class="bg-white" style="position:fixed;width:100vw;bottom:0;z-index: 1024;border-top-left-radius:20upx;border-top-right-radius:20upx;">
				<view class="cu-bar bg-white" style="border-top-left-radius:20upx;border-top-right-radius:20upx;">
					<view class='action text-bold'>
						{{i18n.deviceController.port}} | {{i18n.deviceController.subport}} {{i18n.deviceController.set}}
					</view>
				</view>
				<form @submit="changeParam" :data-setting="setPortItem.setting" :data-value="setPortItem.value" :data-childPort="setPortItem.childPort"
				 data-change="childport">
					<view class="cu-form-group" style="border-bottom: 1upx solid rgba(0, 0, 0, 0.1);">
						<view class="title">{{i18n.deviceController.subport}}: </view>
						<input :disabled="setPortItem.disabled" :placeholder="setPortItem.childPort" name="childport"></input>
						<button :disabled="setPortItem.disabled" class="cu-btn line-grey shadow" form-type="submit">{{i18n.confirm}}</button>
					</view>
				</form>
				<form @submit="changeParam" :data-setting="setPortItem.setting" :data-port="setPortItem.port" :data-value="setPortItem.value"
				 data-change="port">
					<view class="cu-form-group" style="border-bottom: 1upx solid rgba(0, 0, 0, 0.1);">
						<view class="title">{{i18n.deviceController.port}}: </view>
						<input :disabled="setPortItem.disabled" :placeholder="setPortItem.port" name="port"></input>
						<button :disabled="setPortItem.disabled" class="cu-btn line-grey shadow" form-type="submit">{{i18n.confirm}}</button>
					</view>
				</form>
				<button class="cu-btn block line-grey margin lg" @tap="closeSetPortModal">{{i18n.close}}</button>
			</view>
		</view>

		<!-- <view style="position:fixed;width:100vw;height:100vh;bottom:0;z-index: 1024;background: rgba(0, 0, 0, 0.2)">
			<view class="bg-white" style="position:fixed;width:100vw;bottom:0;z-index: 1024;border-top-left-radius:20upx;border-top-right-radius:20upx;">
				<view class="cu-bar bg-white" style="border-top-left-radius:20upx;border-top-right-radius:20upx;">
					<view class='action text-bold'>
						{{item.settingName}}
					</view>
				</view>
				<view class='flex align-center flex-wrap margin-lr'>
					<view v-for="(arrItem,arrIndex) in item.paramList" :key="arrIndex">
						<view v-for="(item,index) in arrItem" :key="index">
							<button size='mini' @tap="changeParam('fixed')" :data-item="item" :data-arrItem="arrItem" :style="(item.value==item2.value?'background:#8799a3;color:#fff;margin-right:20upx;margin-bottom:20upx':'border: 1upx solid rgba(135, 153, 163, 0.5);color:#8799a3;margin-right:20upx;margin-bottom:20upx')"></button>
						</view>
					</view>
				</view>
				<button class="cu-btn block line-grey margin lg" @tap="closeMoreButtonModal">{{language.cancelBtn}}</button>
			</view>
		</view> -->
	</view>
</template>

<script>
	import * as deviceControllerApi from '@/common/utils/ztnUniAppApi/ztnRequestApi/deviceApi/deviceControllerApi/deviceControllerApi.js'
	import * as deviceCameraApi from '@/common/utils/ztnUniAppApi/ztnRequestApi/deviceApi/deviceCameraApi/deviceCameraApi.js'
	const hours = [];
	const minutes = [];
	const thours = [];
	const tminutes = [];
	for (let i = 0; i < 24; i++) {
		hours[i] = i
	}
	for (let i = 0; i < 60; i++) {
		minutes[i] = i
	}
	for (let i = 0; i < 24; i++) {
		thours[i] = i
	}
	for (let i = 0; i < 60; i++) {
		tminutes[i] = i
	}
	export default {
		data() {
			return {
				isToHome: false,
				fromLoginBack: false,
				isLogin: false,
				CustomBar: this.CustomBar,
				// deviceName: "",
				// deviceCore: "",
				// 监控
				showVideo: false,
				cameraList: [],
				cameraListModalStyle: "",
				showCameraListModal: false,
				videoType: "",
				playUrl: "",
				playerContext: null,
				direction: "vertical",
				isPlay: false,
				playIcon: '../../static/images/cam/play.png',
				pauseIcon: '../../static/images/cam/pause.png',
				// 控制
				style: "",
				settingList: [],
				multiArray: [hours, minutes, thours, tminutes],
				multiIndex: [0, 0, 0, 0],
				deviceError: "",
				showErrorModal: false,
				setPortItem: null,
			}
		},
		props: {
			deviceName: {
				type: String,
				default: ""
			},
			deviceCore: {
				type: String,
				default: ""
			}
		},
		computed: {
			i18n() {
				return this.$i18nMsg()
			},
			themei18n() {
				return this.$themei18nOption()
			}
		},
		onReady: function() {
			// #ifdef H5
			// var video = document.createElement('video')
			// video.id = 'video'
			// video.style = "width:100%;height:100%;position:absolute;top:50%;left: 50%;transform: translate(-50%,-50%);"
			// video.autoplay = true
			// video.controls = true
			// var source = document.createElement('source')
			// source.src = this.playUrl
			// source.type = this.videoType
			// video.appendChild(source)
			// this.$refs.video.$el.appendChild(video)
			// videojs("video")
			// #endif
			// #ifdef MP-WEIXIN
			this.playerContext = uni.createLivePlayerContext('livePlayer', this);
			// #endif
		},
		mounted: function(option) {
			var CustomBar = this.CustomBar
			this.liveplayerStyle = `top:${CustomBar}px`

			var isLogin = uni.getStorageSync('isLogin') || false
			this.isLogin = isLogin
			this.initOption = option
			isLogin ? this.init(option) : uni.navigateTo({
				url: '/pages/login/login'
			})
			
			// this.themeColor = this.themei18n.emphasizeColor
			
		},
		onShow: function() {
			this.$uniUtilsApi.toHome(this)
			var initOption = this.initOption
			var fromLoginBack = this.fromLoginBack
			fromLoginBack && (this.init(initOption), this.fromLoginBack = false, this.isLogin = true)
		},
		methods: {
			init: function(option) {
				// this.deviceName = option.deviceName
				// this.deviceCore = option.deviceCore
				this.getSettingList()
				this.getCameraList()
			},
			getSettingList: function() {
				var deviceCore = this.deviceCore
				deviceControllerApi.getSettingList(this, deviceCore).then((res) => {
					if (res.code == '200') {
						var data = res.data.filter((item, index, arr) => item.hidden == false)
						if (data.length < 1) {
							var title = this.i18n.noData
							this.$uniUtilsApi.showToast(title, 'none', 1000, true)
							setTimeout(function() {
								uni.navigateBack({
									delta: 1
								});
							}, 1000);
							return
						}
						for (let i = 0; i < data.length; i++) {
							if (data[i].settingType == 2) {
								data[i].value = data[i].value > 0 ? false : true
							} else if (data[i].settingType == 4) {
								if (data[i].value != null && data[i].value != "") {
									let value = data[i].value.split(" / ");
									let fvalue = value[0].split(":");
									let svalue = value[1].split(":");
									data[i].multiIndex = fvalue.concat(svalue)
								} else {
									data[i].multiIndex = [0, 0, 0, 0]
								}
							} else if (data[i].settingType == 6) {
								var cmdList = data[i].setting.split(' ');
								data[i].setting = [];
								for (var j = 0; j < cmdList.length; j++) {
									let ce = cmdList[j].split(':');
									let item = {
										'cName': ce[0],
										'eName': ce[1],
										'param': ce[2],
										'now': false,
									}
									if (data[i].value == ce[2]) {
										item.now = true;
									}
									data[i].setting.push(item)
								}
							}
						}
						this.settingList = data
					}
				})
			},
			changeParam: function(e) {
				var data = e.currentTarget.dataset
				var change = data.change
				var nowSetting = data.setting || ""
				var nowPort = data.port || ""
				var nowChildPort = data.childPort || ""
				switch (change) {
					case 'custom':
						let customValue = e.detail.value.custom
						this.saveSetting(customValue, nowSetting, nowPort, nowChildPort)
						break;
					case 'fixed':
						let fixedValue = data.value
						this.saveSetting(fixedValue, nowSetting, nowPort, nowChildPort)
						break;
					case 'switch':
						let switchValue = e.detail.value ? '1' : '0'
						this.saveSetting(switchValue, nowSetting, nowPort, nowChildPort)
						break;
					case 'timeSet':
						let timeSetValue = e.detail.value
						this.saveSetting(timeSetValue, nowSetting, nowPort, nowChildPort)
						break;
					case 'periodTime':
						let index = e.detail.value;
						let hour = this.multiArray[0][index[0]]
						let minute = this.multiArray[1][index[1]]
						let thour = this.multiArray[2][index[2]]
						let tminute = this.multiArray[3][index[3]]
						let periodTime = hour + ':' + minute + ' / ' + thour + ':' + tminute
						this.saveSetting(periodTime, nowSetting, nowPort, nowChildPort)
						break;
					case 'nop':
						this.saveSetting(null, nowSetting, nowPort, nowChildPort)
						break;
					case 'slider':
						var sliderValue = e.detail.value;
						this.saveSetting(sliderValue, nowSetting, nowPort, nowChildPort)
						break;
					case 'port':
						let portValue = data.value
						let portData = e.detail.value.port
						this.saveSetting(portValue, nowSetting, portData, nowChildPort)
						this.closeSetPortModal()
						break;
					case 'childport':
						let childPortValue = data.value
						let childPortData = e.detail.value.childport
						this.saveSetting(childPortValue, nowSetting, nowPort, childPortData)
						this.closeSetPortModal()
						break;
					default:
				}
			},
			saveSetting: function(value, setting, port, childPort) {
				var deviceCore = this.deviceCore
				deviceControllerApi.saveSetting(this, deviceCore, value, setting, port, childPort).then((res) => {
					if (res.code == '200') {
						this.$uniUtilsApi.showToast(this.i18n.setup_successfully, 'none', 1000, true)
						this.getSettingList()
					} else if (res.code == '502') {
						this.saveSettingError(res.code2)
					}
				})
			},
			saveSettingError: function(code) {
				this.showErrorModal = true
				switch (code) {
					case '1':
						this.deviceError = this.i18n.deviceController.wrongPort //'端口有误'
						break;
					case '2':
						this.deviceError = this.i18n.deviceController.wrongCommand //'端口有误'
						break;
					case '3':
						this.deviceError = this.i18n.deviceController.wrongParameter //'参数有误'
						break;
					case '4':
						this.deviceError = this.i18n.deviceController.setup_failed //'设置失败'
						break;
					case '5':
						this.deviceError = this.i18n.deviceController.hardwareNotSupport //'硬件不支持'
						break;
					case '602':
						this.deviceError = this.i18n.deviceController.operateTooFast //'操作过快'
						break;
					case '603':
						this.deviceError = this.i18n.deviceController.equipmentOffline //'设备不在线'
						break;
					case '604':
						this.deviceError = this.i18n.deviceController.deviceResponseTimeout //'设备回应超时'
						break;
					case '606':
						this.deviceError = this.i18n.deviceController.serviceNotSupport //'服务不支持'
						break;
				}
			},
			hideErrorModal: function() {
				this.showErrorModal = false
			},
			openSetPortModal: function(item) {
				this.setPortItem = item
			},
			closeSetPortModal: function(e) {
				this.setPortItem = null
			},
			getCameraList: function() {
				var deviceCore = this.deviceCore
				var userId = uni.getStorageSync("userId")
				deviceCameraApi.getCameraList(this, userId, deviceCore).then((res) => {
					if (res.code == '200') {
						this.cameraList = res.data
					}
				})
			},
			openCameraListModal: function() {
				var CustomBar = this.CustomBar
				this.cameraListModalStyle = `top:${CustomBar}px;height:calc(100vh - {{CustomBar}}px)`
				this.showCameraListModal = true
			},
			hideCameraListModal: function() {
				this.showCameraListModal = false
			},
			tapCamera: function(e) {
				var that = this
				var deviceId = e.deviceId
				this.pushCloud(deviceId)
				// this.hideCameraListModal()
				// var CustomBar = this.CustomBar + 'px'
				// var screenHeight = wx.getStorageSync("screenHeight") + 'px'
				// this.style = `height:calc(${screenHeight} - 225px - ${CustomBar});margin-top:225px`
				// this.showVideo = true
				// this.playUrl = "rtmp://58.200.131.2:1935/livetv/hunantv"
				// this.videoType = 'rtmp'
				// this.isPlay = true
			},
			pushCloud: function(deviceId) {
				deviceCameraApi.pushCloud(this, deviceId).then((res) => {
					if (res.code == '200') {
						var playUrl = res.data.playUrl
						if (playUrl != null) {
							var mediaType = res.data.mediaType
							this.videoType = mediaType == 'm3u8' || mediaType == 'httpflv' ? mediaType : 'rtmp'
							this.playUrl = playUrl
							this.isPlay = true
							var CustomBar = this.CustomBar + 'px'
							var screenHeight = wx.getStorageSync("screenHeight") + 'px'
							this.style = `height:calc(${screenHeight} - 225px - ${CustomBar});margin-top:225px`
							this.showVideo = true
						} else {
							this.$uniUtilsApi.showToast(this.i18n.camera.invalidAddress, 'none', 1000, true) //'播放地址无效'
						}
					} else {
						if (res.code == '201') {
							switch (res.msg) {
								case 'serverCompany':
									this.$uniUtilsApi.showToast(this.i18n.camera.equipmentNotSupported, 'none', 1000, true) //'设备不支持'
									break
								case 'OFFLINE':
									this.$uniUtilsApi.showToast(this.i18n.camera.equipmentOffline, 'none', 1000, true) //'设备不在线'
									break
								default:
									this.$uniUtilsApi.showToast(this.i18n.camera.pushFailure, 'none', 1000, true) //'推流失败'
									break
							}
						} else {
							this.$uniUtilsApi.showToast(this.i18n.camera.pushFailure, 'none', 1000, true) //'推流失败'
						}
					}
				})
			},
			closeVideo: function() {
				this.showVideo = false
				this.style = ""
			},
			tapPlay: function() {
				var isPlay = this.isPlay
				isPlay ? this.playerContext.pause() : this.playerContext.resume()
				this.isPlay = !isPlay
			},
			statechange: function(e) {
